.post-card{
    border: 2px solid var(--border-color);
    border-radius: 15px;
    display: flex;
    flex-direction: column;       /* stack title, desc, and button vertically */
    text-align: left;
    padding: 1rem 1rem; /* bigger space within the cards*/
    margin: 1rem 0; /* puts a gap between cards*/
    max-width: 350px; /* Max width on larger screens */
    min-height: 150px;
    width: 90%;
    overflow: hidden;
    background-color: var(--header-bg);
}

.post-card h3 {
  margin: 0;
}

.post-card p {
  margin: 0.5rem 0 0 0;
  line-height: 1.2;
}

.post-content {
  flex-direction: column;
  text-align: center;
}

.post-content h2{
  text-decoration: underline;
}

.no-results{
  border: 2px solid var(--border-color); /* colour */
  border-radius: 15px;
  padding: 0.5rem 1rem;
  background-color: var(--header-bg);
}