.profile-banner{
    display: flex;
    border-radius: 15px; /* rounds the edges */
    border: 2px solid #000000;
    background-color: var(--header-bg); /* colour */
    padding: 0.5rem 0.5rem; /* bigger space within the cards*/
    width: 90%;
    max-width: 375px;
    justify-content: center;
    color: var(--text-color);
}

.profile-posts{
    background-color: var(--profile-posts-color);
    width: 100%;
    max-width: 375px;
    height: 90%;
    border: 2px solid #000000;
    border-radius: 15px; /* rounds the edges */
    text-align: center;
    color: var(--profile-posts-text-color);
}
